@prefixCls: medo-text-edit;

.@{prefixCls} {

  &-layout {
    width: 100%;
    position: relative;
  }

  &-options {
    margin-left: 8px;
    color: @primary-color;
    cursor: pointer;
  }

  &-textarea {
    width: 100%;
    position: relative;
    left: -12px;
    z-index: 100;
  }

  &-content, &-placeholder {
    font-size: 14px;
    margin: 6px 0;
  }

  &-placeholder {
    color: @gray;
    font-style: italic;
  }

  &-bold {
    font-weight: bold;
  }
}

@primary-color: #00796B;@theme-color: #00796B;@tint-color: #009688;@light-color: #B2DFDB;@gray: #BDBDBD;@dark-gray: #757575;@light-gray: #e8e8e8;@black: #212121;@white: #f0f0f0;