.layout {
  padding: 0 25%;
}
.layout .empty {
  text-align: center;
}
.layout .empty .title {
  color: #00796B;
  font-weight: bold;
  font-size: 22px;
  margin-bottom: 20px;
}
.layout .empty .tips {
  color: #BDBDBD;
  font-size: 14px;
  margin-top: 20px;
}
.layout .empty .tag {
  position: relative;
  left: 0px;
  top: 0px;
  display: inline-block;
  margin: 3px 8px;
  padding: 3px 8px;
  border-radius: 5px;
  background-color: #00796B;
  color: #f0f0f0;
  cursor: pointer;
  box-shadow: 3px 3px 3px #BDBDBD;
  transition: 0.3s all;
}
.layout .empty .tag:hover {
  position: relative;
  left: -2px;
  top: -2px;
  box-shadow: 5px 5px 5px #BDBDBD;
}
