.medo-item-box-layout,
.medo-item-box-sub {
  display: flex;
  justify-content: flex-start;
}
.medo-item-box-sub {
  margin-left: 8%;
}
.medo-item-box-title-index,
.medo-item-box-prefix {
  margin: 6px 0;
  text-align: right;
}
.medo-item-box-prefix {
  margin-right: 10px;
}
.medo-item-box-prefix-work {
  color: #00796B;
}
.medo-item-box-input {
  width: 100%;
}
.medo-item-box-options {
  width: 100%;
  text-align: center;
  padding: 3px;
  margin-top: 5px;
  font-size: 16px;
}
.medo-item-box-options-icon {
  margin: 0 5px;
  color: #757575;
  transition: 0.4s all;
}
.medo-item-box-options-icon:hover {
  color: #00796B;
}
.medo-item-box-add {
  text-align: center;
  padding: 3px;
  margin: 5px 0;
  border-radius: 5px;
  color: #00796B;
  background-color: #fff;
  font-size: 16px;
  cursor: pointer;
  transition: 0.2s all;
}
.medo-item-box-add:hover {
  background-color: #00796B;
  color: #f0f0f0;
}
.medo-item-box-bold {
  font-weight: bold;
}
